:root {
  --header-blue: #3766bf;
}

@font-face {
  font-family: 'tilt-prism';
  src: url('../fonts/TiltPrism.ttf');
}

@font-face {
  font-family: 'game-menu';
  src: url('../fonts/GameMenu.ttf');
}

* {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, 
      Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  list-style: none;
}

#root {
  position: relative;
  min-height: 100vh;
}

.homePage {
  min-height: 100vh;
  background-image: url('../imgs/homeBackground.svg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.homePage h1 {
  color: white;
  font-size: 6.5rem;
  font-family: 'tilt-prism';
  font-weight: 300;
  text-align: center;
  text-shadow: 2px 2px 4px black;
  margin-top: 10vh;
}

.menuLinks {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
  gap: 5vh;
}

.menuLinks a {
  position: relative;
  text-decoration: none;
  color: white;
  border: 2px solid white;
  border-radius: 12px;
  text-align: center;
  padding: 10px 30px;
  text-shadow: 2px 2px 4px black;
  background-color: rgba(0, 0, 0, 0.15);
}

.menuLinks a:hover {
  box-shadow: 0 0 24px rgba(0, 0, 0, 0.9);
  scale: 1.2;
  background-color: white;
  color: black;
  text-shadow: none;
}

.menuLinks a:hover::after,
.menuLinks a:hover::before {
  position: absolute;
  content: '';
  top: 20px;
  width: 0; 
  height: 0; 
  border-top: 20px solid transparent;
  border-bottom: 20px solid transparent; 
}

.menuLinks a:hover::before {
  left: -40px;
  border-left: 20px solid white; 
}

.menuLinks a:hover::after {
  right: -40px;
  border-right: 20px solid white; 
}

.menuLinks li {
  font-family: 'game-menu';
  font-size: 3rem;
  font-weight: 300;
}

.mainGame {
  position: relative;
  min-height: 100vh;
}

.gameHeader {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 95px;
  top: 0;
  position: sticky;
  background-color: var(--header-blue);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.8);
  z-index: 50;
}

.gameHeader div,
.leaderboardHeader div,
.leaderboardHeader h2,
.instructionsHeader div,
.instructionsHeader h2 {
  font-family: 'game-menu';
}

.currentLevel {
  color: white;
  font-size: 2.5rem;
  display: flex;
  align-items: center;
  gap: 20px;
}

.gameTimer {
  text-align: start;
  color: white;
  font-size: 2.5rem;
  display: flex;
  align-items: center;
  width: 310px;
}

.currentTarget {
  display: flex;
  align-items: center;
  gap: 10px;
  position: relative;
  border-radius: 50%;
  background-color: white;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.8) inset;
}

.currentTarget img {
  height: 80px;
  max-width: 100%;
  margin: 0 auto;
  display: block;
}

.targetTooltip {
  position: absolute;
  z-index: 15;
  background-color: white;
  pointer-events: none;
  font-size: 2.5rem;
  padding: 10px;
  border-radius: 8px;
  border: 1px solid black;
  top: 0;
  left: 95px;
  width: 110px;
  height: 80px;
  text-align: center;
  font-family: 'game-menu';
  color: black;
  line-height: 35px
}

.targetTooltip::before,
.targetTooltip::after {
  content: "\0020";
  display: block;
  position: absolute;
  top: 15px;
  left: -22px;
  z-index: 12;
  width: 0;
  height: 0;
  overflow: hidden;
  border: solid 22px transparent;
  border-left: 0;
  border-right-color: #FFF;
}

.targetTooltip::before {
  top: 15px;
  left: -23px;
  z-index: 11;
  border-right-color: black;
}

.gameHeader a,
.leaderboardHeader a,
.instructionsHeader a {
  text-decoration: none;
  color: white;
  font-size: 2.5rem;
}

.gameHeader a:hover,
.leaderboardHeader a:hover,
.instructionsHeader a:hover {
  color: rgba(255, 255, 255, 0.5);
}

.level {
  min-height: calc(100vh - 95px);
  position: relative;
  cursor: none;
  overflow-x: hidden;
  overflow-y: hidden;
}

.levelImgContainer {
  z-index: 1;
  position: relative;
}

.level img {
  max-width: 100vw;
  height: auto;
}

.badGuess {
  position: fixed;
  top: 160px;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  width: 240px;
  padding: 8px 0;
  text-align: center;
  border: none;
  background-color: rgba(255, 255, 255);
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0 0 12px rgba(0, 0, 0, 0.8);
}

.badGuess p {
  font-family: 'game-menu';
  font-size: 2.5rem;
}

#circleCursor {
  height: 100px;
  width: 100px;
  border: 2px solid black;
  position: absolute;
  border-radius: 50%;
  pointer-events: none;
  z-index: 1000;
  background-color: rgba(255, 255, 255, 0.2);
}

.leaderboardHeader,
.instructionsHeader {
  display: flex;
  justify-content: space-between;
  padding: 0 4vw;
  align-items: center;
  height: 95px;
  top: 0;
  position: sticky;
  background-color: var(--header-blue);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.8);
  z-index: 50;
}

.leaderboardHeader h2,
.instructionsHeader h2 {
  color: white;
  font-size: 2.5rem;
  font-weight: 300;
}

.returnMain {
  word-spacing: -8px;
}

.leaderboardKey,
.timeListItem {
  display: flex;
  justify-content: space-between;
  padding: 0 10vw 10px;
  margin-left: 4vw;
  margin-right: 4vw;
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
}

.leaderboardKey {
  margin-top: 20px;
  font-weight: bold;
  font-size: 1.5rem;
}

.timeListItem {
  margin-top: 10px;
  font-size: 1.5rem;
}

.leaderboard ol p {
  font-family: 'game-menu';
}

.timeRank {
  width: 20%;
  text-align: start;
}

.timeName {
  width: 55%;
  text-align: start;
  overflow: hidden;
}

.timeTime {
  width: 25%;
  text-align: end;
}

.instructionsMain {
  min-height: calc(100vh - 95px);
  background-image: url('../imgs/levels/beach.jpg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10vh 15vw;
}

.rules {
  display: flex;
  flex-direction: column;
  gap: 30px;
  background-color: rgba(255, 255, 255, 0.98);
  flex-grow: 1;
  padding: 24px;
  border-radius: 12px;
  box-shadow: 0 0 12px black;
}

.rules img {
  width: 150px;
  margin-left: 20px;
}

.rules h3 {
  color: var(--header-blue);
  font-family: 'game-menu';
  font-size: 2rem;
}

.rules p {
  font-size: 1.3rem;
  color: var(--header-blue);
  margin-left: 20px;
}

.gameRecap {
  min-height: 100vh;
  background-image: url('../imgs/endGame2.jpg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5vh;
  text-align: center;
  min-width: 650px;
}

.gameRecap h2 {
  font-size: 6rem;
  font-family: 'game-menu';
  font-weight: 400;
  margin-bottom: 12vh;
  padding-top: 20px;
}

.gameRecap p {
  font-size: 3.5rem;
  font-family: 'game-menu';
  margin-bottom: 5vh;
}

.playerNameEntry {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2vh;
}

.playerNameEntry label {
  font-family: 'game-menu';
  font-size: 1.5rem;
}

.playerNameEntry input {
  text-align: center;
  border: 1px solid white;
  outline: none;
  padding: 8px 0;
  border-radius: 8px;
  font-size: 3rem;
  font-family: 'game-menu';
  background: none;
  color: white;
  margin-bottom: 30px;
}

.playerNameEntry input:hover {
  box-shadow: 0 0 12px rgba(255, 255, 255);
}

.playerNameEntry input:focus {
  box-shadow: 0 0 12px rgba(255, 255, 255);
}

.gameRecap button {
  border: 1px solid white;
  outline: none;
  padding: 16px 20px;
  border-radius: 8px;
  cursor: pointer;
  font-family: 'game-menu';
  font-size: 2.5rem;
  background: none;
  color: white;
}

.gameRecap button:hover {
  background-color: rgba(255, 255, 255);
  color: black;
}

.gameOverButtons {
  display: flex;
  gap: 4vw;
}